import { combineReducers } from 'redux';
import getManifest from './get-manifest';
import { reducer as formReducer } from 'redux-form';
import nav from './nav';
import player from './player';

export default combineReducers({
  getManifest,
  nav,
  player,
  form: formReducer
});



